export class ProjectModel {
  projectName: string;
  duration: string;
  initiative: string;
  aim: string;
  technologyStack: string;
  imageUrl: string;
  imageStyle: string;

  constructor(projectName: string, imageUrl: string, imageStyle: string,
              duration: string, initiavtive: string, aim: string,
              technologyStack: string) {
    this.projectName = projectName;
    this.duration = duration;
    this.initiative = initiavtive;
    this.aim = aim;
    this.technologyStack = technologyStack;
    this.imageUrl = imageUrl;
    this.imageStyle = imageStyle;
  }
}

export class SmallProject {
  title: string;
  imageUrl: string;
  imageClass: string;
  discription: string;

  constructor(title: string, imageUrl: string, imageClass: string, discription: string){
    this.title = title;
    this.imageUrl = imageUrl;
    this.imageClass = imageClass;
    this.discription = discription;
  }
}
