import { I18nContext } from "@angular/compiler/src/render3/view/i18n/context";

export class AboutDataModel {
	title: string;
  	content: string;

  	constructor(title: string , content: string){
		this.title = title;
	  	this.content = content;
	}
}

export class ExperienceDataModel {
  date: string;
  name: string;
  place: string;
  description: string;

  constructor(date: string, name: string, place: string, description: string){
    this.date = date;
    this.name = name;
    this.place = place;
    this.description = description;
  }

  render() {
    return `
    <div>
    <h4>${this.date}</h4>
    <h3>${this.name}</h3>
    <h5>${this.place}</h5>
    <p>${this.description}</p>
    </div>
    `
  }

}

export class SkillModel {
  skillName: string;
  skillStrength: number;
  skillIcon: string;

  constructor(skillName: string, skillStrength: number, skillIcon: string){
    this.skillName = skillName;
    this.skillStrength = skillStrength;
    this.skillIcon = skillIcon;

  }

}
